import{ useState, form } from 'react';
import '../Mint/App.css';
import './Glass.css';
import { Box, Button, Flex, Spacer, Input, Text } from "@chakra-ui/react";
import { ethers } from "ethers";

const listGlassValve = {
    "0x3604226674a32b125444189d21a51377ab0173d1": require("../info.json").glassesValve
    };

const listName = {
    "0xed75af74cf9ca5a98a37ddba36f37cf28949a089": "Welcome, Daniil",
    "0x3fd67e4c6aeb513128475ff1788a6d90c666fc5c": "Welcome, Dmitriy",
    "0x01bd218794a8ea20d2b53a43cb16fdcc19fbdac3": "Welcome, Amir",
    "0x3604226674a32b125444189d21a51377ab0173d1": "Welcome, Gleb"
}

function Connect({accounts, setAccounts, Hello, setHello, pageValue, setPageValue}) {
    const isConnected = Boolean(accounts[0]);

    function sayHello(){
        if (accounts[0] in listName){
            setHello(listName[accounts[0]]);
        }

    }

    async function ConnectF(){
        if(window.ethereum) {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });  
            setAccounts(accounts);
            sayHello();
        }
    }
    
    function changeSelect() {
        setPageValue(document.getElementById('page').value);
        console.log(pageValue);
    }

    return (
        <div >
            <p>{Hello}</p>
            <select id='page' value={pageValue} onChange={changeSelect}>
                <option>GlassesFactory</option>
                <option>GlassesValve</option>
                <option>Glass</option>
            </select>
            <div>
            {
                isConnected ? (
                    <Button onClick = {ConnectF}>
                    Update data
                </Button>    
                ):(
                <Button onClick = {ConnectF}>
                    Connect
                </Button>
                )
            }
        </div>
            <div className="moving-background"> </div>
        </div>
    );
  }

  export default Connect;