import{ useState, form } from 'react';
import '../Mint/App.css';
import './Glass.css';
import { Box, Button, Flex, Spacer, Input, Text } from "@chakra-ui/react";
import { ethers } from "ethers";
import GlassesValveABI from "../../abi/GlassesValve.json";

function Valve() {
    const [accounts, setAccounts] = useState([]);
    const [inputList, setInputList] = useState([{ address: "", percent: "" }]);
    const [strGlasses, setStrGlasses] = useState("Download Data ...");
    const [Valid, setValid] = useState(false);
    const [GlassData, setGlassData] = useState('');
    const [Balance, setBalance] = useState('');
    

// handle input change
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
    };
    
    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };
    
    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { address: "", percent: "" }]);
    };

    async function addGlasses(){
        console.log('AddGlasses');
        var glassesAddress = document.querySelectorAll('[id="address"]');
        var glasses = new Array();
        for (var i = 0; i< glassesAddress.length; i++){
            console.log(glassesAddress[i].value);
            glasses.push(glassesAddress[i].value);
        }
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            var ValveAddress = document.getElementById('valveAddress').value;;
            const contract = new ethers.Contract(
            ValveAddress,
            GlassesValveABI,
            signer
            );
            console.log(glasses);
            try {
                let response = await contract.addGlasses(glasses);
                console.log(response);
            } catch (err) {
                console.log("error: ", err);
            }
        }
    }

    async function downloadInfo(){
        
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            var ValveAddress = document.getElementById('valveAddress').value;;
            const contract = new ethers.Contract(
                ValveAddress,
            GlassesValveABI,
            signer
            );
            try {
                let amountOfGlasses = await contract.amountOfGlasses();
                var sum = 0;
                for(var i = 0; i < amountOfGlasses ; i++){
                    let st = await contract.glasses(i)
                    sum += st.border - st.fullness;
                }
                let balance = await contract.balance();
                setGlassData("Amount of Glasses: " + amountOfGlasses.toString() + ";    Amount to fill " + sum.toString());
                setBalance("Balance: " + balance.toString());
            } catch (err) {
                console.log("error: ", err);
            }    
        }
    }

    async function fillGlasses(){
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            var ValveAddress = document.getElementById('valveAddress').value;;
            const contract = new ethers.Contract(
            ValveAddress,
            GlassesValveABI,
            signer
            );
            try {
                let response = await contract.fillGlass();
                console.log(response);
            } catch (err) {
                console.log("error: ", err);
            } 
        }       
    }

    async function downloadGlass(){
        const valveAddress = document.getElementById('valveAddress').value;
        setValid(false);
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
            valveAddress,
            GlassesValveABI,
            signer
            );
            try {
                let owner= await contract.owner();
                setValid(true);
                console.log(owner);
            } catch (err) {
                console.log("error: ", err);
            } 
        } 
        downloadInfo();

    }
    
    return (
        <div>
            <Text className='baseText' fontFamily="VT323">
                Glasses Valve
            </Text>
            <Input id = "valveAddress" onChange={downloadGlass}>
            </Input>
            <Button onClick={downloadGlass}>
                Update Glass
            </Button>       
            <div>
              {
              Valid ? (
                    <div >
                        {inputList.map((x, i) => {
                return (
                <div className="box" id = 'inputGlasses' align = 'center'>
                    <input
                        name="address"
                        id = 'address'
                        type='text'
                        placeholder="Enter Address"
                        value={x.address}
                        onChange={e => handleInputChange(e, i)}
                    />
                    <div >
                        {inputList.length !== 1 && <Button className = 'addRemovebutton'
                        onClick={() => handleRemoveClick(i)}>Remove</Button>}
                        {inputList.length - 1 === i && <Button className = 'addRemovebutton' onClick={handleAddClick}>Add</Button>}
                    </div>
                </div>
            );
            })} 
                    <div align = 'center'>
                        <Button className = 'chkraButton' onClick={addGlasses}>
                            Add Glasses 
                        </Button>
                        <Text className='baseText' fontFamily="VT323" padding = '0px'>
                       ------------------------------------
                        </Text>
                        <Text className='baseText' fontFamily="VT323" padding = '0px'>
                            {GlassData}
                        </Text>
                        <Text className='baseText' fontFamily="VT323" padding = '0px'>
                            {Balance}
                        </Text>
                        <Button className = 'chkraButton' onClick={fillGlasses}>
                            Fill Glasses 
                        </Button>
                    </div> 
                    </div>
                    ):(
                        <Text className='baseText' fontFamily="VT323" padding = '0px'>
                        Address isn't correct
                        </Text>
                    )
                }  
            </div>

            <div className="moving-background"> </div>
        </div>
    );
  } 
  
export default Valve;