import { useState } from "react";
import { ethers } from "ethers";
import { Box, Button, Flex, Spacer, Input, Text, Select, option } from "@chakra-ui/react";
import TSM from "../../abi/ITSM.json";
import TokenABI from "../../abi/Token.json";
import './App.css';

const TSMAddress = require("./../info.json").address.TSM;

const MaintMint = ({ accounts, setAccounts }) => {
  const [value, setValue] = useState(0);
  const isConnected = Boolean(accounts[0]);
  const [verified, setVerifid] = useState('Enter Address to check ');
  const [addedUser, setAddedUser] = useState('Enter refferal to add ')
  const [tokenValue, setTokenValue]= useState('0x7d56289E27E653C8ec7D17E06cd291132c522B85');

  async function checkUser(){
    const regUser = document.getElementById('regUser').value;
    console.log(regUser);
      
    if (window.ethereum) {
          const provider = new ethers.providers.Web3Provider(window.ethereum);
          const signer = provider.getSigner();
          const contract = new ethers.Contract(
              TSMAddress,
              TSM,
              signer
      );
      try {
          var valid = true;
          try {
            ethers.utils.getAddress(regUser)
          } catch(err){
            valid = false
          }
          if (valid){
          setVerifid('download data ...');
          console.log(regUser.toString());
          let response = await contract.checkUser(regUser);
          console.log((response));
          if (response == true)
              setVerifid(regUser + '\t is Verified');
          else
              setVerifid(regUser + '\t is NOT Verified');
          }
          else
            setVerifid("Address is not correct");
      } catch (err) {
          console.log("error: ", err);
          }
      }
  }

  async function addUser(){
    if (window.ethereum) {
        const regUser = document.getElementById('addUser').value;
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const contract = new ethers.Contract(
            TSMAddress,
            TSM,
            signer
    );
    try {
        var valid = true;
        try {
          ethers.utils.getAddress(regUser)
        } catch(err){
          valid = false
        }
        if (valid){
        setAddedUser('download data ...');
        console.log(regUser.toString());
        let response = await contract.checkUser(regUser);
        console.log((response));
        if (response == true)
            setAddedUser(regUser + '\t is Added (Verifiyed)');
        else
            setAddedUser(regUser + '\t is Added (NOT Verifiyed)');
        }
        else
          setAddedUser("Address is not correct");
    } catch (err) {
        console.log("error: ", err);
        }
    }
}
  
  const handleChange = (amount) => {
    if (amount <= 0 )
      alert("Amount should be greater then 0");  
  };

  async function Count(){
    if (window.ethereum) {
      const amount = document.getElementById('totalAmt').value;
      handleChange(amount);
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        TSMAddress,
        TSM,
        signer
      );
      try {
          let response = await contract.amountToSend(ethers.utils.parseEther((amount).toString()));
          console.log((response.toString()));
          setValue(Math.floor((response.toString()/10**12))/10**6);
      } catch (err) {
        console.log("error: ", err);
      }
    }
  }

  async function Approve() {
    const amount = ethers.utils.parseEther(document.getElementById('approveAmount').value).toString();
    const tokenAddress = document.getElementById('approveToken').value;
    console.log(amount, tokenAddress);
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        tokenAddress,
        TokenABI,
        signer
      );
      try {
        let response = await contract.approve(TSMAddress, amount);
        return ("response: ", response);
      } catch (err) {
        console.log("error: ", err);
      }
    }
  }

  async function handleMint() {
    const regUser = document.getElementById('addUser').value;
    const amount = ethers.utils.parseEther(document.getElementById('totalAmt').value).toString();
    const token = document.getElementById('approveToken').value;
    console.log()
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        TSMAddress,
        TSM,
        signer
      );
      try {
        var valid = true;
        var response;
        try {
          ethers.utils.getAddress(regUser)
        } catch(err){
          valid = false
        }
        console.log(valid)
        if (!valid)
          response = await contract.buyWithoutRef(token, amount);
        else
          response = await contract.buyWithRef(token, amount, regUser);
        return ("response: ", response);
      } catch (err) {
        console.log("error: ", err);
      }
    }
  }

  function changeSelect() {
    setTokenValue(document.getElementById('approveToken').value);
  }

  return (
    <div className="main-mint">
    <Flex justify="center" align="center" height="20vh" >
      <Box width="520px">
        <div>
          <Text fontSize="48px">Token Sale Machine </Text>
          <Text
            fontSize="30px"
            letterSpacing="-5.5%"
            fontFamily="VT323"
          >
            This website is for demo purposes only.
          </Text>
        </div>
      </Box>
    </Flex>
    <Flex justify="center" align="center" height="25vh">
      <div>
          <Input
              className="c-address-input"
              id="regUser"
              type="string"
            />

          <Button
              className='c-button'
              onClick = {checkUser}
              > Check User Address
          </Button>
          <p>
              {verified}
          </p>

          <Input
              className="c-address-input"
              id="addUser"
              type="string"
            />

          <Button
              className='c-button'
              onClick = {addUser}
              > Add User Address
          </Button>
          <p>
              {addedUser}
          </p>

          
      </div>
    </Flex>
    <Flex justify="center" align="center" height="25vh">
      {isConnected ? (
        <div>
          <Button
            className="c-button"
            onClick={Count}
          >
            Get token amount: {value}
          </Button>

          <Flex justify="center" align="center">
            <select className='styledSelect' id='approveToken' height='40px' value={tokenValue} onChange={changeSelect}>
                  <option height='40px'>0x7d56289E27E653C8ec7D17E06cd291132c522B85</option>
                  <option height='40px'>0x3dC13Cfea91E8b3Fb64c14a853Ae4aB1D17299b6</option>
            </select>
            <Input
              borderRadius="10px"
              className="c-amount-input"
              id="totalAmt"
              type="number"
              step="1"
            />
          <Button
            className="c-button"
            onClick={handleMint}
          >
            Mint Now
          </Button>
          </Flex>
          <Flex>
            <select className='styledSelect' id='approveToken' height='40px' value={tokenValue} onChange={changeSelect}>
                  <option height='40px'>0x7d56289E27E653C8ec7D17E06cd291132c522B85</option>
                  <option height='40px'>0x3dC13Cfea91E8b3Fb64c14a853Ae4aB1D17299b6</option>
            </select>
            <Input
              borderRadius="10px"
              className="c-amount-input"
              id="approveAmount"
              type="number"
              step="1"
            />
              <Button className="c-button" onClick={Approve}>
                  Approve
              </Button>
          </Flex>

        </div>
        ) : (
          <Text
            marginTop="70px"
            fontSize="30px"
            letterSpacing="5.5%"
            fontFamily="VT323"
            color="#008fd4"
          >
            Connect your wallet to mint.
          </Text>
        )}

    </Flex>

    </div>
  );
};

export default MaintMint;