import{ useState, form } from 'react';
import '../Mint/App.css';
import './Freezer.css';
import { Box, Button, Flex, Spacer, Input, Text } from "@chakra-ui/react";
import { ethers } from "ethers";
import IFreezer from "../../abi/Freezer.json";
import TSM from "../../abi/ITSM.json";

const FreezerAddress = require("./../info.json").address.Freezer;
const TSMAddress = require("./../info.json").address.TSM;


function Freezer() {
    const [accounts, setAccounts] = useState(['']);
    const [isConnected, setConnected] = useState(Boolean(accounts[0]));
    const [withdrawAmount, setwithdrawAmount] = useState('0');
    const [Verifiyed, setVerifiyed] = useState('False');
    

    async function Withdraw(){
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
            FreezerAddress,
            IFreezer,
            signer
            );
            try {
                let response = await contract.withdraw();
                console.log(response);
            } catch (err) {
                console.log("error: ", err);
            } 
        }       
    }

    async function getWithdrawAmount(){
        if(window.ethereum) {
            setAccounts(accounts);
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
            FreezerAddress,
            IFreezer,
            signer
            );
        console.log("getWithdrawAmount");
        let signerAddress = await signer.getAddress();
        try {
            let response = await contract.claimList(signerAddress);
            console.log("withDraw: ", response/10**18);
            setwithdrawAmount(response/10**18);
        } catch (err) {
            console.log("error: ", err);
        } 
    }
    }

    async function checkUser(){
        console.log("checkUser");
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                TSMAddress,
                TSM,
                signer
        );
        setVerifiyed('download data ...');
        let signerAddress = await signer.getAddress();
        console.log(signerAddress);
        try {
            let response = await contract.checkUser(signerAddress);
            console.log((response));
            if ((response))
                setVerifiyed("True");
            else
                setVerifiyed("False");

        } catch (err) {
            console.log("error: ", err);
            }
        }
    }

    async function Connect(){
        if(window.ethereum) {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });  
            console.log(accounts);
            setAccounts(accounts);
            setConnected(Boolean(accounts[0]));
            await getWithdrawAmount();
            await checkUser();
        }
        console.log(isConnected);
    }
    
    return (
        <div className="overlay">
            <div>
            </div>

            <div>
                {isConnected ? (
                <div>
                    <div>
                    <Text className='baseText' fontFamily="VT323">
                        You're Balance: {withdrawAmount}
                    </Text>
                    </div>
                    <div>
                    <Text className='baseText' fontFamily="VT323">
                        You're Verifiyed: {Verifiyed}
                    </Text>
                    <Button className = 'chkraButton' onClick={Withdraw}>
                        Withdraw 
                    </Button>
                    </div>
                    <div>
                    <Text className='baseText' fontFamily="VT323">
                        {accounts[0]}
                    </Text>
                    </div>
                    <div >

                    <Button className = 'chkraButton' onClick={Connect} >
                        Update data
                    </Button>
                </div>
                </div>
                ):(
                <div>
                    <Button className = 'chkraButton' onClick={Connect} >
                        Connect
                    </Button>
                </div>)
                }  
            </div>
            
            <div className="moving-background"> </div>
        </div>
    );
  }
  
  
export default Freezer;