import{ useState, form } from 'react';
import '../Mint/App.css';
import './Glass.css';
import { Box, Button, Flex, Spacer, Input, Text } from "@chakra-ui/react";
import { ethers } from "ethers";
import GlassABI from "../../abi/Glass.json";

function Glass() {
    const [Valid, setValid] = useState(false);
    const [balance, setBalance] = useState('');
    const [owner, setOwner] = useState(''); 
    const [border, setBorder] = useState(''); 
    const [fullness, setFullness] = useState(''); 

    async function Withdraw(){
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            var GlassAddress = document.getElementById('glassAddress').value;
            const contract = new ethers.Contract(
            GlassAddress,
            GlassABI,
            signer
            );
            try {
                let response = await contract.Withdraw();
                console.log(response);
            } catch (err) {
                console.log("error: ", err);
            } 
        }       
    }

    async function downloadGlass(){
        const glassAddress = document.getElementById('glassAddress').value;
        setValid(false);
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
            glassAddress,
            GlassABI,
            signer
            );
            try {
                let balance = await contract.balance();
                let owner = await contract.owner();
                let border = await contract.border();
                let fullness = await contract.fullness();
                setValid(true);
                setFullness(fullness.toString());
                setBalance(balance.toString());
                setBorder(border.toString());
                setOwner(owner);
                console.log(balance, owner, border, fullness);
            } catch (err) {
                console.log("error: ", err);
            } 
        } 
    }


    return (
        <div>
            <div>
            <Text className='baseText' fontFamily="VT323">
                Glass
            </Text>
            <Input id = "glassAddress" onChange={downloadGlass}>
            </Input>
            <Button onClick={downloadGlass}>
                Update Glass
            </Button>
              {
                Valid ? 
                (
                    <div >
                        <Text className='baseText' fontFamily="VT323">
                            Balance: {balance}
                        </Text>
                        <Text className='baseText' fontFamily="VT323">
                            Owner: {owner}
                        </Text>
                        <Text className='baseText' fontFamily="VT323">
                            Border: {border}
                        </Text>
                        <Text className='baseText' fontFamily="VT323">
                            Fullness: {fullness}
                        </Text>
                    </div>
                ):(
                    <Text className='baseText' fontFamily="VT323">
                        Address isn't correct
                    </Text>
                )  
              }  
            </div>
            <div>
            <Button className = 'chkraButton' onClick={Withdraw}>
                Withdraw 
            </Button>
            </div>
            
            <div className="moving-background"> </div>
        </div>
    );
  }
  
  
export default Glass;